/*======================================================
smothscroll
 ======================================================*/

ul.smothscroll {
    position: fixed;
    bottom: 25px;
    right: 30px;
    list-style: none;
    z-index: 9999;
}

ul.smothscroll a {
    background-color: rgba(254, 62, 87, 0.7);
    display: block;
    width: 45px;
    height: 45px;
    line-height: 45px;
    color: #fff;
    border: 2px solid #fe3e57;
    border-radius: 45px;
    text-align: center;
    -webkit-transition: all 0.2s;
    transition: all 0.2s;
}

ul.smothscroll a:hover{
    background-color: #fe3e57;
}


@media(max-width:767px){
    ul.smothscroll a {
        width: 30px;
        height: 30px;
        line-height: 30px;
    }
}
.mobileMenu {
    position: fixed;
    left: -280px;
    top: 0;
    z-index: 9999;
    height: 100vh;
    width: 280px;
    background:#070640;
    -webkit-transition: all 0.3s ease 0s;
    transition: all 0.3s ease 0s;
