/* Navigation */

nav {
	float: left;
	padding-left: 82px;
}
nav ul {
	margin-top: -4px;
	margin-bottom: 0;
}
nav > ul > li {
	display: inline-block;
	padding: 30px 19px;
	margin-right: -1px;
	position: relative;
}
nav ul li a {
	display: inline-block;
	color: #fff !important;
	font-size: 15px;
	font-weight: 600;
}
nav ul li:hover > ul {
	opacity: 1;
	visibility: visible;
}
nav ul li:hover > ul li {
	margin-top: 0;
}
nav ul li a:hover,
nav ul li a.active {
	color: #fe3e57!important;
	text-decoration: none;
}

nav ul ul {
	position: absolute;
	top: 100%;
	left: 0;
	width: 200px;
	-ms-box-shadow: 0 0 20px rgba(0,0,0,0.08);
	-o-box-shadow: 0 0 20px rgba(0,0,0,0.08);
	box-shadow: 0 0 20px rgba(0,0,0,0.08);
	opacity: 0;
	visibility: hidden;
	z-index: 999;
}
nav ul ul li {
	padding: 10px 15px;
	background-color: #fff;
	border-bottom: 1px solid #eaeaea;
	margin-top: 20px;
}
nav ul ul li:last-child {
	border-bottom: 0;
	position: relative;

}
nav ul ul ul {
	left: 100%;
	top: 0;
}

@media(max-width:1200px){
	nav ul ul ul {
		left: -100%;
		top: 0;
	}
}
