    background: linear-gradient(#ea4c89, #ea4c89);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.bg-social ul li a:before {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: #fff;
    opacity: .3;
    z-index: 9;
